@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/InterRegular.woff2") format("woff2"), url("../../fonts/InterRegular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
  src: url("../../fonts/Inter-SemiBold.woff2") format("woff2"), url("../../fonts/Inter-SemiBold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "GT Walsheim Pro";
  font-style: normal;
  font-weight: 500;
  src: url("../../fonts/GTWalsheimProMedium.woff2") format("woff2"), url("../../fonts/GTWalsheimProMedium.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.link {
  text-decoration: none;
}

.button {
  border: none;
  background-color: transparent;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  cursor: pointer;
}

.page {
  height: 100%;
  font-family: "Inter", "Arial", "Helvetica", sans-serif;
  font-size: 16px;
}

.page__body {
  margin: 0;
}

.page-body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  color: #cecece;
  background-color: #ffffff;
}

.page-header {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 36px;
  padding: 17px 21px 46px 38px;
  background-color: #1d2532;
}
.page-header::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 50%;
  opacity: 0.35;
  box-shadow: 0 0 137px 137px #5f38ff, 0 0 137px 137px #ff0ede;
  z-index: 1;
}
@media (min-width: 1920px) {
  .page-header::before {
    top: 94px;
    left: 230px;
    box-shadow: 0 0 173px 108px #5f38ff, 0 0 173px 148px #ff0ede;
  }
}
@media (min-width: 768px) {
  .page-header {
    padding: 20px 35px 48px;
  }
}
@media (min-width: 1920px) {
  .page-header {
    width: 1260px;
    padding: 30px 340px 120px;
  }
}

.page-header__logo {
  position: relative;
  display: block;
  z-index: 2;
}
.page-header__logo::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  margin: auto;
  width: 27px;
  height: 27px;
  border-radius: 50%;
  background-image: linear-gradient(163deg, #dea8ff, #9d6ad9, #7a2bf6);
  border: 1px solid #7a2bf6;
  box-sizing: border-box;
  z-index: 1;
}
@media (min-width: 1920px) {
  .page-header__logo::before {
    width: 41px;
    height: 41px;
    left: -20px;
  }
}

.page-header__logo-image {
  display: block;
  padding-left: 35px;
}
@media (min-width: 1920px) {
  .page-header__logo-image {
    width: 73px;
    height: 29px;
  }
}

.page-nav__toggle {
  display: block;
  background-color: transparent;
  border: none;
  height: 40px;
  width: 40px;
}
.page-nav__toggle::before {
  content: "";
  position: absolute;
  top: 30px;
  right: 16px;
  width: 40px;
  height: 3px;
  border-radius: 10px;
  background-color: #ffffff;
  box-shadow: 0 13px 0 0 #ffffff;
}
@media (min-width: 768px) {
  .page-nav__toggle {
    display: none;
  }
}

.page-nav__wrapper {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  width: 95%;
  margin-left: 16px;
  padding-top: 115px;
  background-color: #293344;
  border-bottom-left-radius: 20px;
  z-index: 1;
}
@media (min-width: 768px) {
  .page-nav__wrapper {
    position: relative;
    display: flex;
    align-items: flex-start;
    gap: 15px;
    flex-wrap: wrap;
    width: 100%;
    padding-top: 0;
    background-color: transparent;
  }
}
@media (min-width: 1920px) {
  .page-nav__wrapper {
    gap: 15px 127px;
  }
}

.page-nav__list {
  margin-bottom: 90px;
  margin-left: 22px;
}
@media (min-width: 768px) {
  .page-nav__list {
    margin-bottom: 0;
  }
}

.page-nav__user-login {
  margin-bottom: 60px;
  margin-left: 21px;
}
@media (min-width: 768px) {
  .page-nav__user-login {
    margin-bottom: 0;
  }
}

.page-nav--opened .page-nav__toggle::before, .page-nav--opened .page-nav__toggle::after {
  content: "";
  position: absolute;
  top: 41px;
  right: 24px;
  width: 30px;
  height: 3px;
  border-radius: 10px;
  background-color: #ffffff;
  box-shadow: 0 13px 0 0 #ffffff;
  z-index: 2;
}
.page-nav--opened .page-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.page-nav--opened .page-nav__toggle::after {
  transform: rotate(-45deg);
  box-shadow: none;
}

.page-nav--opened .page-nav__wrapper {
  display: block;
}
@media (min-width: 768px) {
  .page-nav--opened .page-nav__wrapper {
    display: flex;
  }
}

@media (max-width: 319px) {
  .page-nav--closed .page-nav__wrapper {
    display: block;
  }

  .page-nav--opened .page-nav__toggle {
    display: none;
  }
}
.page-nav--nojs .page-nav__toggle {
  display: none;
}

.page-nav--nojs .page-nav__wrapper {
  display: block;
}
@media (min-width: 768px) {
  .page-nav--nojs .page-nav__wrapper {
    display: flex;
  }
}

@media (min-width: 768px) {
  .nav-list {
    display: flex;
    align-self: center;
    gap: 15px;
    flex-wrap: wrap;
  }
}
@media (min-width: 1920px) {
  .nav-list {
    gap: 15px 107px;
  }
}

.nav-list__item:not(:last-of-type) {
