.modal-container {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(48, 9, 97, 0.35);
  z-index: 4;
  visibility: hidden;
}

.modal-container__modal {
  position: relative;
  margin: auto;
}

.modal-container--opened {
  visibility: visible;
  backdrop-filter: blur(7px);
}

.modal {
  width: 320px;
  background-color: #2b3647;
  border-radius: 20px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .modal {
    width: 567px;
  }
}
@media (min-width: 1920px) {
  .modal {
    width: 670px;
  }
}

.modal__close-button {
  position: absolute;
  top: 22px;
  right: 16px;
  display: block;
  width: 34px;
  height: 34px;
}
.modal__close-button::before {
  content: "";
  position: absolute;
  top: 16px;
  left: 7px;
  width: 20px;
  height: 3px;
  background-color: #ffffff;
  transform: rotate(45deg);
  border-radius: 10px;
}
.modal__close-button::after {
  content: "";
  position: absolute;
  top: 16px;
  left: 7px;
  width: 20px;
  height: 3px;
  background-color: #ffffff;
  transform: rotate(-45deg);
  border-radius: 10px;
}
@media (min-width: 1920px) {
  .modal__close-button {
    width: 45px;
    height: 45px;
    top: 31px;
    right: 40px;
  }
  .modal__close-button::before {
    top: 16px;
    left: 7px;
    width: 35px;
  }
  .modal__close-button::after {
    top: 16px;
    left: 7px;
    width: 35px;
  }
}

.modal__content {
  margin: 45px 16px;
}
@media (min-width: 1920px) {
  .modal__content {
    margin: 37px 124px 90px 70px;
  }
}

.modal-content {
  position: relative;
  color: #ffffff;
  z-index: 1;
}

.modal-content__title {
  position: relative;
  top: 9px;
  font-size: 23px;
  margin: 0 0 25px;
}
@media (min-width: 1920px) {
  .modal-content__title {
    font-size: 45px;
    letter-spacing: -1px;
    margin: 0 0 25px;
  }
}

.modal-content__description {
  font-size: 18px;
  margin: 0 0 25px;
}
@media (min-width: 768px) {
  .modal-content__description {
    max-width: 460px;
  }
}
@media (min-width: 1920px) {
  .modal-content__description {
    font-size: 21px;
    margin: 0 0 35px;
  }
}

.modal-form {
  position: relative;
  top: 5px;
  display: flex;
  flex-direction: column;
  gap: 17px;
}

.modal-form__input {
  width: 100%;
  padding: 15px;
  font-size: 16px;
  background-color: #ffffff;
  border: none;
  border-radius: 10px;
  box-sizing: border-box;
}
@media (min-width: 1920px) {
  .modal-form__input {
    font-size: 18px;
  }
}

.modal-form__submit-button {
  display: block;
  font-family: "GT Walsheim Pro", "Arial", "Helvetica", sans-serif;
  width: 100%;
  font-size: 16px;
  color: #242424;
  padding: 15px 30px;
  margin: 0 auto;
  background-image: linear-gradient(to right, #1dd844, #cbf62e);
  border-radius: 15px;
  box-shadow: 0 0 20px 0 #1dd844;
}
.modal-form__submit-button::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 50%;
  opacity: 0.35;
  box-shadow: 0 25px 300px 70px #cbf62e;
  z-index: -1;
}
@media (min-width: 1920px) {
  .modal-form__submit-button::after {
    box-shadow: 0 -163px 300px 150px #cbf62e;
  }
}
.modal-form__submit-button:hover {
  box-shadow: 0 0 20px 0 #1dd844, 0 0 20px 0 #cbf62e;
}
.modal-form__submit-button:active {
  box-shadow: inset 0 4px 4px 0 rgba(0, 0, 0, 0.2), 0 0 20px 0, 0 0 20px 0 #cbf62e;
}
@media (min-width: 1920px) {
  .modal-form__submit-button {
    font-size: 18px;
  }
}

.modal-form__submit-button[disabled=disabled] {
  color: rgba(36, 36, 36, 0.5);
  box-shadow: none;
}